    submitCouponCode(inCouponCode: string, inFrom: string) {
        if (!inCouponCode) {
            alert('코드를 입력 해 주세요.')
            return;
        }

        axios.get(`https://us-central1-casetebook.cloudfunctions.net/confirmPromocode?promocode=${inCouponCode}`).then((response: any) => {
            // console.log(response.data, response.data.result === false);
            if (response.data.result === true) {
                this.buyFbSubscriptionInfo(response.data.term);


                setTimeout(() => {
                    if (inFrom === 'mypage') {
                        alert('정상 등록 완료!');
                    } else {
                        alert('정상 등록 완료!');
                    }
                    data.hideAlert();
                    window.location.reload();
                }, 1000);
            } else if (response.data.result === false) {
                errorAlert(response.data.info);
            }
        }).catch((err: any) => {
            errorAlert('');
        })
        function errorAlert(cd: string) {
            // faillen // 길이오류 (변조)
            // failotk // 이런이벤트 없음(변조) 
            // failtermcd // 기간코드가 이상함 (변조)
            // failused // 이미사용 (재사용)
            // failpushstore // 스토어에 코드 등록하다 에러 (시스템오류)
            // failnotbeing // 발행하지 않은 코드 
            let errorMessage = '';
            // alert(cd);
            if (['faillen', 'failotk', 'failtermcd', 'failnotbeing'].includes(cd)) {
                alert('유효하지 않은 코드 입니다. \n다시 확인 해 주세요');
            } else if (cd === 'failused') {
                alert('이미 사용한 코드입니다.\n 사용하지 않은 코드인 경우 이메일 혹은 카카오 채널로 문의 주시면 신속히 확인해드리겠습니다.')
            } else {
                alert('등록과정에서 오류가 발생하였습니다. \n 잠시 후 다시 시도해 주세요.');
            }

        }
        // https://us-central1-casetebook.cloudfunctions.net/confirmPromocode?promocode=3qySWyoEwI
    }

    buyFbSubscriptionInfo(inTermcode: string) {

        let onlyTermCode = inTermcode;

        if (inTermcode.substr(0, 1) === 't') {
            // t1m, t1w, t24w
            onlyTermCode = inTermcode.substr(1);
        }

        let termNum = 1;
        let weekMonthCD: 'm' | 'w' = 'm';
        let weekMonthCDFull: 'month' | 'week' = 'month';
        if (onlyTermCode.length === 3) {
            // 10자리가 넘어가는 12w 같은경우 
            termNum = parseInt(onlyTermCode.substr(0, 2));
            weekMonthCD = onlyTermCode.substr(2) as 'm' | 'w';
        } else {
            // 10자리 이하 
            termNum = parseInt(onlyTermCode.substr(0, 1));
            weekMonthCD = onlyTermCode.substr(1) as 'm' | 'w';
        }
        if (weekMonthCD === 'w') weekMonthCDFull = 'week';

        // 현재 구독중인 상품이 없거나 지난경우 
        let startdateStr = Days.utc().local().format('YYYYMMDD');

        const currentAvailableSubscriptionInfoArr = this.currentAvailableSubscriptionInfo();
        // console.log('tmplog_getSubscriptionState 2ss', currentAvailableSubscriptionInfoArr)
        // 현재 구독중인 상품이 있는경우 
        // 마지막 가능상품 뒤를 시작날짜로 넣는다 
        if (currentAvailableSubscriptionInfoArr.length > 0) {
            // 마지막 가능한 프리티켓 날짜 뽑기 
            const obj = data.convertSubscriptionInfoStrToObj(currentAvailableSubscriptionInfoArr[currentAvailableSubscriptionInfoArr.length - 1]);

            startdateStr = Days(obj.et).add(1, 'day').format('YYYYMMDD');
        }
        let enddayStr = Days(startdateStr).add(termNum, weekMonthCDFull).format('YYYYMMDD');
        const fbasesubscriptionInfo = `${inTermcode}##${startdateStr}##${enddayStr}`;
        this.userMoreInfo.subscriptionInfoHistory.push(fbasesubscriptionInfo);
        this.syncMoreInfo();
    }

    // 현재 구매정보 리턴
    isSubscriptionInfo(): string {
        // subscriptionInfoHistory
        // 로그인이 안 된 경우
        if (!this.isLogin()) {
            return 'needLogin';
        }
        if (this.userMoreInfo.subscriptionInfoHistory.length === 0) {
            return 'needPurchase';
        }
        const currentAvailableSubscriptionInfoArr = this.currentAvailableSubscriptionInfo();
        if (currentAvailableSubscriptionInfoArr.length === 0 || !currentAvailableSubscriptionInfoArr) {
            return 'needPurchase';
        }
        // console.log('tmplog_tmplog_getSubscriptionState ss', currentAvailableSubscriptionInfoArr, currentAvailableSubscriptionInfoArr.length, !currentAvailableSubscriptionInfoArr)
        return currentAvailableSubscriptionInfoArr[0];
    }

    // 현재 및 미래 구독가능한 정보 조회 
    currentAvailableSubscriptionInfo(): string[] {
        const returnArr: string[] = [];
        const todayInt = parseInt(Days.utc().local().format('YYYYMMDD'));
        // console.log('tmplog_getSubscriptionState 3arr', this.userMoreInfo.subscriptionInfoHistory)
        this.userMoreInfo.subscriptionInfoHistory.forEach((infoStr: string) => {
            // t1m##20210212##20210312
            const obj = data.convertSubscriptionInfoStrToObj(infoStr);

            // 오늘이 기간안에 들어있으면 
            if (obj.stInt >= todayInt && todayInt <= obj.etInt) {
                returnArr.push(infoStr);
            }
        })
        return returnArr;

    }


